.Result {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 5vw;
    gap: 1vw;
}

.ResultButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    font-size: 8vw;
    border-radius: 4vw;
    background: #AFDFE4;
    transition: transform .2s;
    &:active{
        transform: scale(.95);
        background: #09DDE0
    }
}

.ResultButton.Push {
    background: #09DDE0
}

.Message {
    font-size: 5vw;
    height: 5vw;
}

.Svg {
    padding-top: 2vw;
    width: 40vw;
}

.Timer {
    font-size: 3vw;
    text-align: left;
    padding-left: 1vw;
}

#path1 {
    stroke: rgb(0,0,0); 
    stroke-width: 5; 
    stroke-dasharray: none; 
    stroke-linecap: butt; 
    stroke-dashoffset: 0; 
    stroke-linejoin: miter; 
    stroke-miterlimit: 4; 
    fill: rgb(255,255,255); 
    fill-opacity: 0; 
    fill-rule: nonzero; 
    opacity: 1;
}

#path2 {
    stroke: rgb(0,0,0); 
    stroke-width: 5; 
    stroke-dasharray: none; 
    stroke-linecap: butt; 
    stroke-dashoffset: 0; 
    stroke-linejoin: miter; 
    stroke-miterlimit: 4; 
    fill: none; 
    fill-rule: nonzero; 
    opacity: 1;
}

#txt {
    stroke: none; 
    stroke-width: 1; 
    stroke-dasharray: none; 
    stroke-linecap: butt; 
    stroke-dashoffset: 0; 
    stroke-linejoin: miter; 
    stroke-miterlimit: 4; 
    fill: rgb(0,0,0); 
    fill-rule: nonzero; 
    opacity: 1; 
    white-space: pre;
}